.library-header {
  height: 230px;
  background-color: #303030;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("mobile-background@1x.b11fd52f.jpg");
  background-position: 20%;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: auto;
  margin-right: auto;
  padding-top: 44px;
}

.library-container {
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.logo {
  align-items: center;
  gap: 12px;
  display: inline-flex;
}

.logo-title {
  font-family: Roboto, sans-serif;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16;
  display: none;
}

.logo:hover, .logo:focus {
  animation: ScaleBig 2s linear;
}

@keyframes ScaleBig {
  0% {
    transform: scale(100%);
  }

  50% {
    transform: scale(120%);
  }

  100% {
    transform: scale(100%);
  }
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  gap: 30px;
  display: flex;
}

.link {
  color: #fff;
  cursor: pointer;
  text-decoration: none;
}

.nav-link {
  padding: 3px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16px;
  position: relative;
}

.nav-title {
  padding: 3px;
}

.nav-link:before, .nav-link:after {
  content: "";
  width: 0;
  height: 2px;
  background: #fff;
  transition: all .2s linear;
  position: absolute;
}

.nav-title:before, .nav-title:after {
  content: "";
  width: 2px;
  height: 0;
  background: #fff;
  transition: all .2s linear;
  position: absolute;
}

.nav-link:hover:before, .nav-link:hover:after {
  width: 100%;
}

.nav-link:hover .nav-title:before, .nav-link:hover .nav-title:after {
  height: 100%;
}

.nav-link:after {
  transition-duration: .4s;
  bottom: 0;
  left: 0;
}

.nav-link .nav-title:after, .nav-link:before {
  transition-duration: .4s;
  top: 0;
  right: 0;
}

.nav-link .nav-title:before {
  transition-duration: .4s;
  bottom: 0;
  left: 0;
}

.current-link {
  position: relative;
}

.current-link:after {
  content: "";
  height: 3px;
  width: 100%;
  background-color: #ff001b;
  display: inline-block;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.library-btn-list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 60px;
  display: flex;
}

.library-btn {
  cursor: pointer;
  color: #fff;
  width: 130px;
  height: 44px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: inline-block;
}

.library-btn:hover, .library-btn:focus {
  background-image: linear-gradient(90deg, #00c0ff 0%, #ffcf00 49%, #fc4f4f 80%, #00c0ff 100%);
  border: none;
  animation: slidebg 5s linear infinite;
}

@keyframes slidebg {
  to {
    background-position: 20vw;
  }
}

.btn-active {
  background: #ff6b01;
  border: none;
  border-radius: 5px;
}

.library-queued-btn-link {
  cursor: pointer;
  text-align: center;
  padding: 13px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

@media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .library-header {
    background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("mobile-background@2x.0d3e7c61.jpg");
  }
}

@media screen and (min-width: 768px) {
  .logo-title {
    display: block;
  }

  .library-header {
    height: 219px;
    background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("tablet-background@1x.b11fd52f.jpg");
  }

  .library-container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .library-btn-list {
    gap: 32px;
    margin-top: 40px;
  }

  .library-btn {
    width: 152px;
    height: 44px;
  }

  @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
    .library-header {
      background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("tablet-background@2x.0d3e7c61.jpg");
    }
  }
}

@media screen and (min-width: 1280px) {
  .library-header {
    height: 216px;
    background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("desktop-background@1x.446dd1ba.jpg");
  }

  .library-container {
    max-width: 1280px;
  }

  .library-btn {
    width: 148px;
    height: 42px;
  }

  .library-btn-list {
    gap: 16px;
  }

  @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
    .library-header {
      background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("desktop-background@2x.cda5f1c8.jpg");
      padding-top: 49px;
    }
  }
}

.arrow-up-icon {
  display: inline-block;
}

.arrow-up-btn {
  width: 40px;
  height: 40px;
  background-color: #ff6b01;
  border: none;
  border-radius: 50%;
  position: fixed;
  bottom: 10px;
  right: 10px;
  box-shadow: 0 1px 5px #303030;
}

.arrow-up-btn:hover, .arrow-up-btn:focus {
  animation: ScaleBig 2s linear;
}

.arrow-up-btn-visible {
  justify-content: center;
  align-items: center;
  display: flex;
}

